import React, { Component } from "react";
import { Row, Col, Container, Button } from "shards-react";
import PageTitle from "../components/common/PageTitle";
import axios from "axios";
import user_id from "./../components/common/getUserId";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { CircularProgressbar } from "react-circular-progressbar";
import "./../../node_modules/react-circular-progressbar/dist/styles.css";
import ImageUploader from "react-images-upload";
import { Redirect } from "react-router";

export default class AddGallery extends Component {
  constructor(props) {
    super(props);
    this.state = { pictures: [] };
    this.onDrop = this.onDrop.bind(this);
  }
  onDrop(picture) {
    var new_pictures = [];

    for (let i = 0; i < picture.length; i++) {
      var file = picture[i];
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => new_pictures.push(reader.result);
    }
    this.setState({
      pictures: new_pictures,
    });
  }

  render() {
    const uploadImgs = () => {
      if (this.state.pictures[0]) {
        const userImages = {
          id: user_id,
          images: this.state.pictures,
        };
        axios
          .post("http://localhost/lumen/public/api/upload-images", userImages)
          .then((response) => {
            if (response.data) {
              toast.success("Image uploaded successfully", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: false,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
              window.location.reload(false);
            } else {
              toast.error("Images not upload !! Try again later", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: false,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
            }
          })
          .catch((error) => {
            console.log(error);
          });
      } else {
        toast.error("Select image first", {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: false,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      }
    };
    // const percentage = 10;
    return (
      <Container fluid className="main-content-container px-4">
        <Row noGutters className="page-header py-4">
          <PageTitle
            title="Edit User Profile"
            subtitle="Overview"
            md="12"
            className="ml-sm-auto mr-sm-auto"
          />
        </Row>
        <Row>
          <Col lg="6" md="8" sm="12">
            <ImageUploader
              withIcon={true}
              withPreview={true}
              buttonText="Choose images"
              onChange={this.onDrop}
              imgExtension={[".jpg", ".gif", ".png", ".jpeg"]}
              maxFileSize={5242880}
            />
            <ToastContainer />
            {/* <CircularProgressbar value={percentage} text={`${percentage}%`} />; */}
            <Button className="mb-3" onClick={() => uploadImgs()}>
              Upload
            </Button>
          </Col>
        </Row>
      </Container>
    );
  }
}