import React from "react";
import { Row, Container, Col, Button } from "shards-react";
import Gallery from "react-grid-gallery";
import PageTitle from "./../components/common/PageTitle";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import user_id from "./../components/common/getUserId";
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css


export default class Trash extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      Deletedimages: [],
      selectedImg: [],
      isImages: false,
    };

    this.onSelectImage = this.onSelectImage.bind(this);
  }
  componentWillMount() {

    this.getImages()
  }

  getImages()
  {
    axios
      .post("http://localhost/lumen/public/api/get-delete-images", {
        id: user_id,
      })
      .then((response) => {
        if (response.data !== "") {
          this.setState({
            Deletedimages: response.data,
          });
          
        } else {
          this.setState({ isImages: false });
        }

        if(this.state.Deletedimages[0])
        {
          this.setState({
            isImages : true,
            selectedImg : [],
          })
        }

      })
      .catch((error) => {
        console.log(error);
      });
  }

  onSelectImage(index, image) {
    var images = [];
    images = this.state.Deletedimages.slice();
    var img = images[index];
    if (img.hasOwnProperty("isSelected")) img.isSelected = !img.isSelected;
    else img.isSelected = true;

    var selected_images = [];
    for (let i = 0; i < images.length; i++) {
      if (images[i].isSelected === true) {
        selected_images.push(images[i].src);
      }
    }

    this.setState({
      selectedImg: selected_images,
    });
  }
  emtpyBin = () => {
    axios
      .post("http://localhost/lumen/public/api/empty-bin", {id:user_id})
      .then((response) => {
        if (response.data) {
          this.setState({
            isImages : false,
          })
        } else {
          toast.error("Trash all ready empty..", {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: false,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }

  submit = () => {
    confirmAlert({
      title: 'Empty bin?',
      message: "All items will be permanently deleted. This can't be undone.",
      buttons: [
        {
          label: 'Cancle',
          onClick: () => ""
        },
        {
          label: 'Empty bin',
          class:'btn btn-info',
          onClick: () => this.emtpyBin()
        }
      ]
    });
  };

  render() {
    const deleteImg = () => {
      const userData = {
        id: user_id,
        images: this.state.selectedImg,
      };

      if (this.state.selectedImg[0]) {
        axios
          .post(
            "http://localhost/lumen/public/api/delete-images-permanently",
            userData
          )
          .then((response) => {
            console.log(response.data);
            if (response.data) {
              toast.success("Image permanently deleted ....", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: false,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
              this.getImages()
            } else {
              toast.error("Images not deleted !! Try again later", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: false,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
            }
          })
          .catch((error) => {
            console.log(error);
          });
      } else {
        toast.error("select image first", {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: false,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      }
    };

    const restoreImg = () => {
      const userData = {
        id: user_id,
        images: this.state.selectedImg,
      };

      if (this.state.selectedImg[0]) {
        axios
          .post(
            "http://localhost/lumen/public/api/restore-images",
            userData
          )
          .then((response) => {
            console.log(response.data);
            if (response.data) {
              toast.success("Image restored deleted ....", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: false,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
              this.getImages()
              //   window.location.reload(false);
            } else {
              toast.error("Images not deleted !! Try again later", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: false,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
            }
          })
          .catch((error) => {
            console.log(error);
          });
      } else {
        toast.error("select image first", {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: false,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      }
    };

    return (
      <Container fluid className="main-content-container px-4">
        <Row noGutters className="page-header py-4">
          <PageTitle
            title="Trash"
            subtitle="Overview"
            md="12"
            className="ml-sm-auto mr-sm-auto"
          />
          <ToastContainer />
          { this.state.isImages ?
          <a href="javascript:void(0)" className="btn btn-outline-danger btn-sm mt-3" onClick={this.submit}>Empty Trash</a>
          : ""
          }
          {
            this.state.selectedImg[0] 
            ?
            <div>
            <Button className="ml-2 btn btn-outline-light btn-sm text-danger mt-3" onClick={deleteImg}>
              Delete
            </Button>
            <Button className="ml-1 btn btn-outline-light btn-sm text-info mt-3" onClick={restoreImg}>
              Restore
            </Button>
            </div> 
            : ""
          }
          
          
        </Row>
        <Row>
          <Col lg="12">
            {this.state.isImages ? (
              <div
                style={{
                  display: "block",
                  minHeight: "1px",
                  width: "100%",
                  border: "1px solid #ddd",
                  overflow: "auto",
                }}
              >
                <Gallery
                  images={this.state.Deletedimages}
                  onSelectImage={this.onSelectImage}
                  lightboxWidth={1536}
                />
              </div>
            ) : (
              "You have no images in Trash"
            )}
          </Col>
        </Row>
      </Container>
    );
  }
}
