import React from "react";
import { Row, Container, Col, Button ,FormCheckbox} from "shards-react";
import Gallery from "react-grid-gallery";
import PageTitle from "./../common/PageTitle";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import user_id from "./../common/getUserId";


export default class ImageGallery extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      images: [],
      selectedImg: [],
      isImages : true
    };

    this.onSelectImage = this.onSelectImage.bind(this);
  }
  componentWillMount() {
    axios
      .post("http://localhost/lumen/public/api/get-user-images", {
        id: user_id,
      })
      .then((response) => {

        if(response.data !== "")
        {
          this.setState({
            images: response.data,
            isImages: true,
          });
        }
        else{
          this.setState({ isImages: false });
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }

 



  onSelectImage(index, image) {
    var images = [];
    images = this.state.images.slice();
    var img = images[index];
    if (img.hasOwnProperty("isSelected")) img.isSelected = !img.isSelected;
    else img.isSelected = true;

    var selected_images = [];
    for (let i = 0; i < images.length; i++) {
      if (images[i].isSelected === true) {
        selected_images.push(images[i].src);
      }
    }

    this.setState({
      selectedImg: selected_images,
    });
  }

  render() {

    const selectAll = () =>{
      var images = [];
      images = this.state.images;

      var img;  
      for(var i= 0 ; i < images.length ; i++)
      {
        img = images[i];
        if (img.hasOwnProperty("isSelected")) img.isSelected = !img.isSelected;
        else img.isSelected = true;
      
      }
      this.setState({
        selectedImg: images,
      });
    }
    
    const deleteImg = () => {
      const userData = {
        id: user_id,
        images: this.state.selectedImg,
      };

      if (this.state.selectedImg[0]) {
        axios
          .post("http://localhost/lumen/public/api/delete-images", userData)
          .then((response) => {
            console.log(response.data);
            if (response.data) {
              toast.success("Image deleted successfully", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: false,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
              window.location.reload(false);
            } else {
              toast.error("Images not upload !! Try again later", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: false,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
            }
          })
          .catch((error) => {
            console.log(error);
          });
      } else {
        toast.error("select image first", {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: false,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      }
    };
    return (
      <Container fluid className="main-content-container px-4">
        <Row noGutters className="page-header py-4">
          <PageTitle
            title="Gallery"
            subtitle="Overview"
            md="12"
            className="ml-sm-auto mr-sm-auto"
          />
          <ToastContainer />
          
          <Button className="btn-danger" onClick={deleteImg}>
            Delete
          </Button>
        </Row>
        <Row>
          <Col lg="12">
            {
              this.state.isImages ? ( 
                <div>
                <FormCheckbox onClick={selectAll}/>
              <div
                style={{
                  display: "block",
                  minHeight: "1px",
                  width: "100%",
                  border: "1px solid #ddd",
                  overflow: "auto",
                }}
              >
                <Gallery
                  images={this.state.images}
                  onSelectImage={this.onSelectImage}
                  lightboxWidth={1536}
                />
              </div>
              </div>
            ) : (
              "You have no images in gallery"
            )} 
          </Col>
        </Row>
      </Container>
    );
  }
}
