import React from "react";
import PropTypes from "prop-types";
import { Navbar, NavbarBrand } from "shards-react";
import axios from "axios";
import { Dispatcher, Constants } from "../../../flux";

class SidebarMainNavbar extends React.Component {
  constructor(props) {
    super(props);
    this.handleToggleSidebar = this.handleToggleSidebar.bind(this);

    this.state = {
      app_name: "",
    };
  }

  componentWillMount() {
    axios
      .get("http://localhost/lumen/public/api/get-app-name")
      .then((response) => {
        let new_app_name = response.data.s_value;
        let local_data = localStorage.getItem("app_data");
        // alert(local_data.app_name);
        if (local_data === null) {
          let app_name = { app_name: new_app_name };
          localStorage.setItem("app_data", JSON.stringify(app_name));
          this.setState({ app_name: new_app_name });
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }

  handleToggleSidebar() {
    Dispatcher.dispatch({
      actionType: Constants.TOGGLE_SIDEBAR,
    });
  }

  render() {
    const { hideLogoText } = this.props;
    let newAPP = localStorage.getItem("app_data");
    newAPP = JSON.parse(newAPP);
    if (newAPP == null) newAPP = this.state.app_name;
    else newAPP = newAPP.app_name;
    return (
      <div className="main-navbar">
        <Navbar
          className="align-items-stretch bg-white flex-md-nowrap border-bottom p-0"
          type="light"
        >
          <NavbarBrand
            className="w-100 mr-0"
            href="#"
            style={{ lineHeight: "25px" }}
          >
            <div className="d-table m-auto">
              <img
                id="main-logo"
                className="d-inline-block align-top mr-1"
                style={{ maxWidth: "25px" }}
                src={require("../../../images/shards-dashboards-logo.svg")}
                alt="Shards Dashboard"
              />
              {!hideLogoText && (
                <span className="d-none d-md-inline ml-1" id="app-name">
                  {newAPP}
                </span>
              )}
            </div>
          </NavbarBrand>
          {/* eslint-disable-next-line */}
          <a
            className="toggle-sidebar d-sm-inline d-md-none d-lg-none"
            onClick={this.handleToggleSidebar}
          >
            <i className="material-icons">&#xE5C4;</i>
          </a>
        </Navbar>
      </div>
    );
  }
}

SidebarMainNavbar.propTypes = {
  /**
   * Whether to hide the logo text, or not.
   */
  hideLogoText: PropTypes.bool,
};

SidebarMainNavbar.defaultProps = {
  hideLogoText: false,
};

export default SidebarMainNavbar;
