import React, { Component } from "react";
import { Row, Col } from "shards-react";
import axios from "axios";
// import { Redirect } from "react-router-dom";

import "./login.css";

export default class Login extends Component {
  state = {
    username: "",
    password: "",
    user_error: "",
    pass_error: "",
    error: "",
    loader: "",
  };

  login(userData) {
    this.setState({
      loader: "fa fa-spinner fa-spin",
    });
    axios
      .post("http://localhost/lumen/public/api/login", userData)
      .then((response) => {
        // console.log(response.data);
        if (response.data !== "") {
          let data = JSON.stringify(response.data);
          localStorage.setItem("login_info", data);
          window.location.href = "/blog-overview";
        } else {
          this.setState({
            error: "email or password are incorrect",
            loader: "",
          });
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }

  handleClick = (e) => {
    e.preventDefault();

    if (this.state.username === "" || this.state.password === "") {
      if (this.state.username === "") {
        this.setState({
          user_error: "Username is required",
        });
      }
      if (this.state.password === "") {
        this.setState({
          pass_error: "Password is required",
        });
      }
    } else if (this.state.username !== "" && this.state.password !== "") {
      this.setState({
        user_error: "",
        pass_error: "",
      });

      // let re =/^(([^<>()[]\\.,;:s@"]+(.[^<>()[]\\.,;:s@"]+)*)|(".+"))@(([[0-9]{1,3}.[0-9]{1,3}.[0-9]{1,3}.[0-9]{1,3}])|(([a-zA-Z-0-9]+.)+[a-zA-Z]{2,}))$/';

      /*if (!re.test(this.state.username)) {
        this.setState({
          user_error: "Email is not valid",
        });
      } else*/ {
        var userData = {
          email: this.state.username,
          password: this.state.password,
        };

        this.login(userData);
      }
    }
  };

  render() {
    return (
      <Row>
        <Col lg="12">
          <form>
            <h3>Sign In</h3>

            <div className="form-group">
              <label>Email address</label>
              <input
                type="email"
                className="form-control"
                placeholder="Enter email"
                onChange={(e) => this.setState({ username: e.target.value })}
              />
              <span class="text-danger font-weight-normal">
                {this.state.user_error}
                {this.state.error}
              </span>
            </div>

            <div className="form-group">
              <label>Password</label>
              <input
                type="password"
                className="form-control"
                placeholder="Enter password"
                onChange={(e) => this.setState({ password: e.target.value })}
              />
              <span class="text-danger font-weight-normal">
                {this.state.pass_error}
              </span>
            </div>

            <div className="form-group">
              <div className="custom-control custom-checkbox">
                <input
                  type="checkbox"
                  className="custom-control-input"
                  id="customCheck1"
                />
                <label className="custom-control-label" htmlFor="customCheck1">
                  Remember me
                </label>
              </div>
            </div>

            <button
              type="button"
              className="btn btn-primary btn-block"
              onClick={(e) => this.handleClick(e)}
            >
              <i className={this.state.loader} style={{ fontSize: "15px" }}></i>
              &nbsp;&nbsp;Submit
            </button>
            <p className="forgot-password text-right">
              Forgot <a href="#">password?</a>
            </p>
          </form>
        </Col>
      </Row>
    );
  }
}
