import React from "react";
import { Container, Row, Col } from "shards-react";
import PageTitle from "../components/common/PageTitle";
import BlogOverview from "./BlogPosts";
import Genral from "../components/settings/Genral";
import Email from "../components/settings/Email";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

const linkStyle = {
  color: "#3F526F",
  textDecoration: "none",
};

const UserProfileLite = () => {
  const handleClick = (e) => {
    var elements = document.getElementsByClassName("custom-link");

    for (let i = 0; i < elements.length; i++) {
      elements[i].classList.remove("activeSetting");
    }
    e.target.classList.add("activeSetting");
  };

  return (
    <Container fluid className="main-content-container px-4">
      <Row noGutters className="page-header py-4">
        <PageTitle
          title="Settings"
          subtitle="Overview"
          md="12"
          className="ml-sm-auto mr-sm-auto"
        />
      </Row>
      <Router>
        <Row>
          <Col lg="3">
            <div className="cardBoxShadow">
              <ul style={{ padding: "0", borderRadius: "10px" }}>
                <Link style={linkStyle} to="/settings/genral">
                  <li
                    className="text-hover listStyle custom-link"
                    onClick={(e) => handleClick(e)}
                  >
                    Genral
                  </li>
                </Link>
                <Link style={linkStyle} to="/settings/email">
                  <li
                    className="text-hover listStyle custom-link"
                    onClick={(e) => handleClick(e)}
                  >
                    Email
                  </li>
                </Link>
                <Link style={linkStyle} to="/settings/sms">
                  <li
                    className="text-hover listStyle custom-link"
                    onClick={(e) => handleClick(e)}
                  >
                    SMS
                  </li>
                </Link>
              </ul>
            </div>
          </Col>
          <Col lg="9">
            <Switch>
              <Route exact path="/settings/genral">
                <Genral />
              </Route>
              <Route path="/settings/email">
                <Email />
              </Route>
              <Route path="/settings/sms">
                <BlogOverview />
              </Route>
            </Switch>
          </Col>
        </Row>
      </Router>
    </Container>
  );
};

export default UserProfileLite;
