import React, { useState } from "react";
import {
  Card,
  ListGroup,
  ListGroupItem,
  Row,
  Col,
  Form,
  FormInput,
  Button,
  Container,
} from "shards-react";
import PageTitle from "../components/common/PageTitle";
import PasswordStrengthBar from "react-password-strength-bar";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const ChangeUserPassword = () => {
  const [oldPassword, setOldPassword] = useState("");
  const [newPassword, setnewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [error, setError] = useState("");
  const [invalid, setInvalid] = useState("");
  const [loader, setLoader] = useState("");

  const handleClick = () => {
    if (oldPassword === "" || newPassword === "" || confirmPassword === "") {
      setInvalid("invalid");
      setError("Fill all fileds");
    } else {
      setInvalid("");
      if (newPassword !== confirmPassword) {
        setError("New password and confirm password not match");
      } else {
        setError("");
        setLoader("fa fa-spinner fa-spin");
        // setDisable(true);
        let user_info = localStorage.getItem("login_info");
        let data = JSON.parse(user_info);

        var userData = {
          id: data.id,
          old_password: oldPassword,
          new_password: newPassword,
        };

        axios
          .post("http://localhost/lumen/public/api/cp", userData)
          .then((response) => {
            setLoader("");
            // setDisable(false);
            if (response.data) {
              document.getElementById("form").reset();
              toast.success("Password changed successfully", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: false,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
            } else {
              toast.error("old password not match", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: false,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
            }
          })
          .catch((error) => {
            console.log(error);
          });
      }
    }
  };

  return (
    <Container fluid className="main-content-container px-4">
      <Row noGutters className="page-header py-4">
        <PageTitle
          title="Change Password"
          subtitle="Overview"
          md="12"
          className="ml-sm-auto mr-sm-auto"
        />
      </Row>
      <Row>
        <Col lg="4" md="6" sm="12">
          <Card small className="mb-4">
            <ListGroup flush>
              <ListGroupItem className="p-3">
                <Row>
                  <Col>
                    <Form id="form">
                      {/* <span className="text-center text-success font-weight-normal">
                        {success}
                      </span> */}
                      <span className="text-center text-danger font-weight-normal">
                        {error}
                      </span>
                      <Row form>
                        <Col lg="12" className="form-group">
                          <label htmlFor="oldPassword">Old Password</label>
                          <FormInput
                            // {...(oldPasswordError ? "invalid" : "")}
                            type="password"
                            id="oldPassword"
                            placeholder="Old Password"
                            {...{ invalid }}
                            onChange={(e) => setOldPassword(e.target.value)}
                          />
                        </Col>
                      </Row>
                      <Row form>
                        <Col lg="12" className="form-group">
                          <label htmlFor="newPassword">New Password</label>
                          <FormInput
                            // {...invalid}
                            type="password"
                            id="newPassword"
                            placeholder="New Password"
                            {...{ invalid }}
                            onChange={(e) => setnewPassword(e.target.value)}
                          />
                          <PasswordStrengthBar password={newPassword} />
                        </Col>
                      </Row>
                      <Row form>
                        <Col lg="12" className="form-group">
                          <label htmlFor="confirmPassword">
                            Confirm Password
                          </label>
                          <FormInput
                            type="password"
                            id="confirmPassword"
                            placeholder="Confirm Password"
                            {...{ invalid }}
                            onChange={(e) => setConfirmPassword(e.target.value)}
                          />
                        </Col>
                      </Row>
                      <ToastContainer />
                      <Button theme="accent" onClick={() => handleClick()}>
                        <i className={loader} style={{ fontSize: "15px" }}></i>
                        &nbsp;&nbsp; Update Password
                      </Button>
                    </Form>
                  </Col>
                </Row>
              </ListGroupItem>
            </ListGroup>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default ChangeUserPassword;
