import React, { Component } from "react";
import {
  Card,
  CardHeader,
  ListGroup,
  ListGroupItem,
  Row,
  Col,
  Form,
  FormGroup,
  FormInput,
  FormSelect,
  FormRadio,
  Button,
} from "shards-react";

export default class Email extends Component {
  constructor(props) {
    super(props);
    this.state = { emailType: "" };
  }

  handleChange = (event) => {
    this.setState({
      emailType: event.target.value,
    });
  };
  inputChangedValue = () => {
    if (this.state.emailType == "smpt") {
      return (
        <div>
          <Row form>
            <Col md="4" className="form-group">
              <label htmlFor="shost">Smtp Host</label>
              <FormInput
                type="text"
                id="shost"
                placeholder="SMTP HOST"
                onChange={() => {}}
              />
            </Col>
            <Col md="4" className="form-group">
              <label htmlFor="sport">Smtp Port</label>
              <FormInput
                type="text"
                id="sport"
                placeholder="SMTP Port"
                onChange={() => {}}
              />
            </Col>
            <Col md="4" className="form-group">
              <label>Smtp Type</label>
              <span className="d-flex">
                <FormRadio type="radio" id="tls" name="radio" />
                <label htmlFor="tls" className="mr-1">
                  TLS
                </label>
                <FormRadio type="radio" id="ssl" name="radio" />
                <label htmlFor="ssl" className="mr-1">
                  SSL
                </label>
              </span>
            </Col>
          </Row>
          <Row form>
            <Col md="6" className="form-group">
              <label htmlFor="username">Username</label>
              <FormInput
                type="text"
                id="username"
                placeholder="SMTP Username"
                onChange={() => {}}
              />
            </Col>
            <Col md="6" className="form-group">
              <label htmlFor="spassword">Password</label>
              <FormInput
                type="password"
                id="spassword"
                placeholder="SMTP Password"
                onChange={() => {}}
              />
            </Col>
          </Row>
          <Row form>
            <Col md="6" className="form-group">
              <label htmlFor="fname">From Name</label>
              <FormInput
                type="text"
                id="fname"
                placeholder="From Username"
                onChange={() => {}}
              />
            </Col>
            <Col md="6" className="form-group">
              <label htmlFor="femail">From Email</label>
              <FormInput
                type="email"
                id="femail"
                placeholder="From Email"
                onChange={() => {}}
              />
            </Col>
          </Row>
        </div>
      );
    } else if (this.state.emailType == "normal") {
      return (
        <Row form>
          <Col md="6" className="form-group">
            <label htmlFor="fname">From Name</label>
            <FormInput
              type="text"
              id="fname"
              placeholder="From Username"
              onChange={() => {}}
            />
          </Col>
          <Col md="6" className="form-group">
            <label htmlFor="femail">From Email</label>
            <FormInput
              type="email"
              id="femail"
              placeholder="From Email"
              onChange={() => {}}
            />
          </Col>
        </Row>
      );
    }
  };
  render() {
    return (
      <Card small className="mb-4">
        <CardHeader className="border-bottom">
          <h6 className="m-0">Email Settings</h6>
        </CardHeader>
        <ListGroup flush>
          <ListGroupItem className="p-3">
            <Row>
              <Col>
                <Form>
                  <Row form>
                    <Col md="4" className="form-group">
                      <label htmlFor="feInputState">Email Type</label>
                      <FormSelect
                        id="feInputState"
                        onChange={this.handleChange}
                      >
                        <option>Choose...</option>
                        <option value="normal">Normal</option>
                        <option value="smpt">SMPT</option>
                      </FormSelect>
                    </Col>
                  </Row>
                  {this.inputChangedValue()}

                  <Button theme="accent">Update</Button>
                </Form>
              </Col>
            </Row>
          </ListGroupItem>
        </ListGroup>
      </Card>
    );
  }
}
