import React, { useState } from "react";
import {
  Card,
  CardHeader,
  ListGroup,
  ListGroupItem,
  Row,
  Col,
  Form,
  FormInput,
  Button,
} from "shards-react";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const Genral = () => {
  const [appName, setAppName] = useState("");
  const handleSubmit = () => {
    if (appName !== "") {
      axios
        .post("http://localhost/lumen/public/api/set-app-name", {
          value: appName,
        })
        .then((response) => {
          document.getElementById("genral").reset();
          if (response) {
            // console.log(response);
            let old_data = localStorage.getItem("app_data");
            old_data = JSON.parse(old_data);
            old_data.app_name = appName;
            localStorage.setItem("app_data", JSON.stringify(old_data));

            document.getElementById("app-name").innerHTML = appName;
            toast.success("App name changed successfully", {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: false,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
          } else {
            toast.error("App name not changed !!", {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: false,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
          }
        })
        .catch((error) => {
          console.log(error);
        });
    } else {
      toast.error("App name is required !!", {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: false,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  };

  return (
    <Card small className="mb-4">
      <CardHeader className="border-bottom">
        <h6 className="m-0">Genral Settings</h6>
      </CardHeader>
      <ListGroup flush>
        <ListGroupItem className="p-3">
          <Row>
            <Col>
              <Form id="genral">
                <Row form>
                  <Col md="6" className="form-group">
                    <label htmlFor="appname">App Name</label>
                    <FormInput
                      id="appname"
                      placeholder="App Name"
                      onChange={(e) => setAppName(e.target.value)}
                    />
                  </Col>
                </Row>
                <ToastContainer />
                <Button theme="accent" onClick={() => handleSubmit()}>
                  Save
                </Button>
              </Form>
            </Col>
          </Row>
        </ListGroupItem>
      </ListGroup>
    </Card>
  );
};

export default Genral;
