import React, { Component } from "react";
import {
  Card,
  ListGroup,
  ListGroupItem,
  Row,
  Col,
  Form,
  FormInput,
  Button,
  Container,
} from "shards-react";
import PageTitle from "../components/common/PageTitle";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Avatar from "react-avatar-edit";

export default class EditUserProfile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      preview: null,
      fname: "",
      lname: "",
      email: "",
      loader: "",
      src: "",
    };
    this.onCrop = this.onCrop.bind(this);
    this.onClose = this.onClose.bind(this);
  }

  onClose() {
    this.setState({ preview: null });
  }

  onCrop(preview) {
    this.setState({ preview });
  }

  componentDidMount() {
    let user_info = localStorage.getItem("login_info");
    user_info = JSON.parse(user_info);
    var user_id = user_info.id;
    axios
      .post("http://localhost/lumen/public/api/get-user-details", {
        id: user_id,
      })
      .then((response) => {
        let user_data = response.data;
        this.setState({
          fname: user_data.first_name,
          lname: user_data.last_name,
          email: user_data.email,
          preview: user_data.profile_pic,
        });
      })
      .catch((error) => {
        console.log(error);
      });
  }
  render() {
    const { fname, lname, email, preview } = this.state;
    const handleClick = () => {
      if (fname === "" || lname === "" || email === "" || preview === null) {
        toast.error("Fill All Fileds !!", {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: false,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      } else {
        this.setState({ loader: "fa fa-spinner fa-spin" });
        let user_info = localStorage.getItem("login_info");
        user_info = JSON.parse(user_info);
        var user_id = user_info.id;
        axios
          .post("http://localhost/lumen/public/api/update-user-profile", {
            id: user_id,
            fname: fname,
            lname: lname,
            email: email,
            image: preview,
          })
          .then((response) => {
            // console.log(response);

            this.setState({ loader: "" });
            if (response.data) {
              document.getElementById("user-name").innerHTML =
                fname + " " + lname;

              document.getElementById("user-img").src = preview;

              toast.success("Profile updated successfully", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: false,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
            } else {
              toast.error("Profile not updated !!", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: false,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
            }
          })
          .catch((error) => {
            console.log(error);
          });
      }
    };
    return (
      <div>
        <Container fluid className="main-content-container px-4">
          <Row noGutters className="page-header py-4">
            <PageTitle
              title="Edit User Profile"
              subtitle="Overview"
              md="12"
              className="ml-sm-auto mr-sm-auto"
            />
          </Row>
          <Row>
            <Col lg="6" md="10" sm="12">
              <Card small className="mb-4">
                <ListGroup flush>
                  <ListGroupItem className="p-3">
                    <Row>
                      <Col>
                        <Form id="form">
                          <Row form>
                            <Col lg="12" className="form-group">
                              <label htmlFor="fname">First Name</label>
                              <FormInput
                                type="text"
                                id="fname"
                                value={fname}
                                placeholder="First Name"
                                onChange={(e) =>
                                  this.setState({ fname: e.target.value })
                                }
                              />
                            </Col>
                          </Row>
                          <Row form>
                            <Col lg="12" className="form-group">
                              <label htmlFor="lname">Last Name</label>
                              <FormInput
                                type="text"
                                id="lname"
                                value={lname}
                                placeholder="Last Name"
                                onChange={(e) =>
                                  this.setState({ lname: e.target.value })
                                }
                              />
                            </Col>
                          </Row>
                          <Row form>
                            <Col lg="12" className="form-group">
                              <label htmlFor="email">Email</label>
                              <FormInput
                                type="text"
                                id="email"
                                placeholder="Email"
                                value={email}
                                onChange={(e) =>
                                  this.setState({ email: e.target.value })
                                }
                              />
                            </Col>
                          </Row>
                          <Row form>
                            <Col lg="12" className="form-group">
                              <label htmlFor="profile">Profile Picture</label>
                              <div className="mt-2 d-flex justify-content-around">
                                <Avatar
                                  height={200}
                                  imageWidth={200}
                                  onCrop={this.onCrop}
                                  onClose={this.onClose}
                                />
                                <img
                                  src={this.state.preview}
                                  height="100"
                                  alt="Preview"
                                />
                              </div>
                            </Col>
                          </Row>
                          <ToastContainer />
                          <Button theme="accent" onClick={() => handleClick()}>
                            <i
                              className={this.state.loader}
                              style={{ fontSize: "15px" }}
                            ></i>
                            &nbsp;&nbsp; Update Profile
                          </Button>
                        </Form>
                      </Col>
                    </Row>
                  </ListGroupItem>
                </ListGroup>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}
