import React from "react";
import { Link } from "react-router-dom";
import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Collapse,
  NavItem,
  NavLink,
} from "shards-react";
import axios from "axios";

export default class UserActions extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      visible: false,
      username: "",
      profile: "",
    };

    this.toggleUserActions = this.toggleUserActions.bind(this);
  }

  componentWillMount() {
    let user_id;
    let login_info = localStorage.getItem("login_info");
    login_info = JSON.parse(login_info);
    if (login_info !== null) user_id = login_info.id;

    axios
      .post("http://localhost/lumen/public/api/get-user-details", {
        id: user_id,
      })
      .then((response) => {
        let data = response.data;
        let full_name = data.first_name + " " + data.last_name;
        let profile_ = data.profile_pic;
        let local_data = localStorage.getItem("app_data");
        if (local_data == null) local_data = {};
        else local_data = JSON.parse(local_data);

        local_data.username = full_name;
        localStorage.setItem("app_data", JSON.stringify(local_data));
        this.setState({ username: full_name });

        local_data.profile = profile_;
        localStorage.setItem("app_data", JSON.stringify(local_data));

        this.setState({ profile: profile_ });
      })
      .catch((error) => {
        console.log(error);
      });
  }

  toggleUserActions() {
    this.setState({
      visible: !this.state.visible,
    });
  }

  render() {
    let userdata = localStorage.getItem("app_data");
    userdata = JSON.parse(userdata);
    let uname, profile;
    if (userdata === null) {
      uname = this.state.username;
      profile = this.state.profile;
    } else {
      uname = userdata.username;
      profile = userdata.profile;
    }
    return (
      <NavItem tag={Dropdown} caret toggle={this.toggleUserActions}>
        <DropdownToggle caret tag={NavLink} className="text-nowrap px-3">
          <img
            className="user-avatar rounded-circle mr-2"
            src={profile}
            id="user-img"
            alt="User Avatar"
          />
          <span className="d-none d-md-inline-block" id="user-name">
            {uname}
          </span>
        </DropdownToggle>
        <Collapse tag={DropdownMenu} right small open={this.state.visible}>
          <DropdownItem tag={Link} to="/user-profile">
            <i className="material-icons">&#xE7FD;</i> Profile
          </DropdownItem>
          <DropdownItem tag={Link} to="edit-user-profile">
            <i className="material-icons">&#xE8B8;</i> Edit Profile
          </DropdownItem>
          <DropdownItem tag={Link} to="/change-password">
            <i className="material-icons">&#xE2C7;</i> Change Password
          </DropdownItem>
          <DropdownItem tag={Link} to="/add-gallery">
            <i className="material-icons">&#xe439;</i> Add Images
          </DropdownItem>
          <DropdownItem divider />
          <DropdownItem tag={Link} to="/logout" className="text-danger">
            <i className="material-icons text-danger">&#xE879;</i> Logout
          </DropdownItem>
        </Collapse>
      </NavItem>
    );
  }
}
