import React from "react";
import { Redirect } from "react-router-dom";

// Layout Types
import { DefaultLayout } from "./layouts";
import CustomLayout from "./layouts/Custom";

// Route Views
import BlogOverview from "./views/BlogOverview";
import Settings from "./views/Settings";
import AddNewPost from "./views/AddNewPost";
import Errors from "./views/Errors";
import ComponentsOverview from "./views/ComponentsOverview";
import Tables from "./views/Tables";
import BlogPosts from "./views/BlogPosts";
import Login from "./views/Login";
import ChangeUserPassword from "./views/ChangeUserPassword";
import EditUserProfile from "./views/EditUserProfile";
import Gallery from "./views/Gallery";
import AddGallery from "./views/AddGallery";
import Trash from "./views/Trash";

let loggedIn = localStorage.getItem("login_info");

const logout = () => {
  localStorage.removeItem("login_info");
  window.location = "/";
};

export default [
  {
    path: "/",
    exact: true,
    layout: DefaultLayout,
    component: () => <Redirect to="/login" />,
  },
  {
    path: "/blog-overview",
    layout: DefaultLayout,
    component: () => (loggedIn ? <BlogOverview /> : <Redirect to="/login" />),
  },
  {
    path: "/user-profile-lite",
    layout: DefaultLayout,
    component: () => (loggedIn ? <Settings /> : <Redirect to="/login" />),
  },
  {
    path: "/add-new-post",
    layout: DefaultLayout,
    component: () => (loggedIn ? <AddNewPost /> : <Redirect to="/login" />),
  },
  {
    path: "/errors",
    layout: DefaultLayout,
    component: () => (loggedIn ? <Errors /> : <Redirect to="/login" />),
  },
  {
    path: "/components-overview",
    layout: DefaultLayout,
    component: () =>
      loggedIn ? <ComponentsOverview /> : <Redirect to="/login" />,
  },
  {
    path: "/tables",
    layout: DefaultLayout,
    component: () => (loggedIn ? <Tables /> : <Redirect to="/login" />),
  },
  {
    path: "/blog-posts",
    layout: DefaultLayout,
    component: () => (loggedIn ? <BlogPosts /> : <Redirect to="/login" />),
  },
  {
    path: "/login",
    layout: CustomLayout,
    component: () => (loggedIn ? <Redirect to="/blog-overview" /> : <Login />),
  },
  {
    path: "/settings",
    layout: DefaultLayout,
    component: () => (loggedIn ? <Settings /> : <Redirect to="/login" />),
  },
  {
    path: "/change-password",
    layout: DefaultLayout,
    component: () =>
      loggedIn ? <ChangeUserPassword /> : <Redirect to="/login" />,
  },
  {
    path: "/logout",
    layout: DefaultLayout,
    component: () => logout(),
  },
  {
    path: "/edit-user-profile",
    layout: DefaultLayout,
    component: () =>
      loggedIn ? <EditUserProfile /> : <Redirect to="/login" />,
  },
  {
    path: "/gallery",
    layout: DefaultLayout,
    component: () => (loggedIn ? <Gallery /> : <Redirect to="/login" />),
  },
  {
    path: "/add-gallery",
    layout: DefaultLayout,
    component: () => (loggedIn ? <AddGallery /> : <Redirect to="/login" />),
  },
  {
    path: "/trash",
    layout: DefaultLayout,
    component: () => (loggedIn ? <Trash /> : <Redirect to="/login" />),
  },
];
